<template>
	<div style="width: 99%;margin-left: 1%;height: calc(100vh);overflow: hidden;">
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  		<div style="margin:20px;">
			<div>
				<input type="search" class="search_ipt" placeholder="请输入关键字" v-model="searchInputValue"><button @click="search({name:searchInputValue},'input')" class="sear_name">搜索</button>
			</div>
			<p><img src="../../assets/hot.png" alt=""style='vertical-align: middle;'>关键字</p>
			<div class="btn_s">
				<button v-for="button in buttons" id="btn_s" :class="{'search-active':button.name == searchValue}" @click="search(button,'button')">{{button.name}}</button>
			</div>
			<!-- 新增按钮 -->
			<div class="next" style="margin-top: 15px;">
				<router-link to="/uploder" slot="right">
					<mt-button class="next-btn" @click="dialogVisible = true" style="border:1px solid #ad181c;background:#fff;color:#101010;"><span style="font-size:20px;color:#737373;">+</span>添加病例</mt-button>
  		  		</router-link>
			</div>
			<hr color="#e6e6e6">
			<div class="warp-scroll" id="dowebok" v-infinite-scroll="loadMore"
				infinite-scroll-disabled="loading" infinite-scroll-distance="5">
				<div v-for='group in groups' class="group_name">
					<div style="color:#999999;font-size:14px;">{{group.record_date}}</div>
					<div class="item_name" >
						<div v-for='item in group.record_content' >
							<img v-bind:src="path+item.thumb_image_path"  @click="scale(path+item.image_path,item)">
							<div style="margin-top:5px;color:#323232;">{{item.name}}</div>
							<div style="color:#b3b3b3;font-size:12px;">{{item.category}}</div>
						</div>
					</div>
				</div>
				<!-- <p v-if="loading"> -->
				<div class="loading-ani" v-if="loading">
					<span class="iconfont icon-jiazai jiazai"></span>
					<span class="loading-msg">正在加载...</span>
				</div>
				<div v-if="nullData" v-cloak>
					<img src="../../assets/wenjian.png" style="width: 60%;margin: 19% 20% 5%;">
					<p style="color:#cccccc;font-size:14px;text-align:center;">您还没有添加任何病例，快来添加吧～</p>
				</div>
			</div>
			<!-- end -->
			<!-- <div class="next" style="margin-top:15px;" v-if="tag">
				<mt-button class="next-btn" @click="loadeMore()" style="border:1px solid #e6e6e6;background:#fff;color:#101010;">加载更多<img src="../../assets/down.png" style="vertical-align:middle;"></mt-button>
			</div> -->
		</div>
		<flexImg :picture="picture" :imgId="imgId" v-if="isClose" v-model="isClose"></flexImg>
	</div>
</template>
<script>
import flexImg from "./flexImg"
import { Indicator } from 'mint-ui'
	export default{
		components:{flexImg},
		data(){
			return {
				searchInputValue:"",
				searchValue:'',
				searchUrl:"all_medical_records?",
				value:null,
				buttons:[{name:'体检报告',id:0},{name:'CT',id:1},{name:'B超',id:2},{name:'血常规',id:3}],
				sheetVisible: false,
				actions: [],
				groups:[],
				isLoading:true,
				isClose:false,
				path: this.$store.state.serverJS,
				picture:'',
				imgId:'',
				page:1,
				tag:false,
				nullData:false,
				loading: false
			}
		},
		computed:{
			dispare (){
				if(this.btnList.length <=0){

					return this.searchList.length<=0?false:true;
				}else{
					return this.btnList.length<=0?false:true;
				}

			}
		},
		watch:{
			'searchValue':function(value){
				this.searchUrl = value==""?"all_medical_records?":this.searchUrl;
				this.groups=[];
				this.page=1;
				this.isLoading= true;
				this.loadMore();
			}
		},

		methods:{
			search (value,type){
				this.searchUrl = type=="button"?"search_two?":"search?";
				this.searchValue =value.name;
				
			},
			loadMore() {
			  	if(!this.isLoading){
			  		return false;
			  	}
				this.loading = true;
				var resource = this.$resource(this.$store.state.serverIP +"/medical_record_managements/"+this.searchUrl+location.search.substring(1).split("&")[0]);
				resource.get({per_page:4,page:this.page,name:this.searchValue}).then((response)=>{	
				  	if(response.data.length>0){
				  		
				  		if(response.data.length>2){
				  			this.page += 1
				  		}else{
				  			this.isLoading= false;
				  		}
				  		this.groups=this.groups.concat(response.data);
				  		
				  	}else{
				  		if(this.searchUrl=="all_medical_records?"){
				  			this.nullData==true;
				  		}	
				  		this.isLoading= false;
				  	}

				  	this.loading = false;
				  	
				 },error=>{
				 	this.isLoading=this.loading= false;
				 	this.$toast('没有更多数据');
				 })
			},
			scale(url,item){
				this.isClose=true;
				this.picture = url;
				this.imgId = item.id;
			},
		},

	}
</script>
<style scoped lang="scss">
	.mint-searchbar-core{
		width: 100%;
	}
	.mint-search{
		height: 9vh !important;
	}
	li{
		list-style: none;
	}
	.btn_s button{
		margin-right: 3%;
		border-radius: 4px;
		background: #f5f5f5;
		color: #999;
		outline: none;
		border: none;
		height: 41px;
		padding: 2% 5%;
		border: none;
	}
	/*.btn_s button:hover,.btn_s button:active{
		background: #ad181c;
		border: none;
		color: #fff;
	}*/
	.search-active{
		background: #ad181c !important;

		color: #fff !important;
	}
	.group_name{
		margin-bottom: 10px;
	    font-size: 20px;
	    z-index: 1;
	}
	.item_name{
		font-size: 16px;
		margin-bottom: 20px;
	}
	.item_name div{
		display: inline-block;
		width: 22%;
		margin-right: 5px;
		line-height: 15px;
	}
	.item_name div>div{
		width:100%;
		line-height:17px;
		/*text-align:center;*/
	}
	.item_name img{
		width:100%;
		height:60px;
		border-radius:5px;
		/*border:1px solid #000;*/
	}
	.search_ipt{
		width:80%;
		background:#f0f0f0;
		display:inline-block;
		border: none;
		border-radius:4px;
		padding:8px;
		margin-right:12px;
		margin-right:6px;
		color: #323232;
	}
	#smark{
		background: rgba(0,0,0,.8);
		position: absolute;
		z-index: 99;
		display: none;
		top:0;
		left:0;
		bottom:0;
	}
	#smark>img{
		width: 50%;
		height: 50%;
		border: none;
		position: relative;
		margin-top: 22%;
	}
	.warp-scroll {
		overflow: auto;
		height:calc(100vh - 40vh)
	}
	.sear_name{
	    border: none;
	    font-weight: bold;
	    background: #ad181c;
	    color: #fff;
	    padding: 5px 2px 5px;
	    border-radius: 5px;
	    width: 17%;
	}
	.loading-ani{
		margin-top: 10px;
		text-align: center;
		.jiazai{
			display: inline-block;
			width: 30px;
			height: 30px;
			text-align: center;
			line-height:30px;
			color:#545454;
			font-size: 28px;
			animation: rotates 1s infinite;
			-moz-animation: rotates 1s infinite;	/* Firefox */
			-webkit-animation: rotates 1s linear 2s infinite ;	/* Safari 和 Chrome */
			-o-animation: rotates 1s infinite;
		}
		.loading-msg{
			margin-left: 20px;
			line-height: 30px;
			display: inline-block;
			height: 30px;
		}
	}
	
	@keyframes rotates {
		0%{
			transform: rotate(0deg);
			-ms-transform: rotate(0deg);		/* IE 9 */
			-webkit-transform: rotate(0deg);	/* Safari and Chrome */
			-o-transform: rotate(0deg);		/* Opera */
			-moz-transform: rotate(0deg);
		};
		100%{
			transform: rotate(360deg);
			-ms-transform: rotate(360deg);		/* IE 9 */
			-webkit-transform: rotate(360deg);	/* Safari and Chrome */
			-o-transform: rotate(360deg);		/* Opera */
			-moz-transform: rotate(360deg);
		}
	}
</style>



